body,
html {
  margin: 0;
  padding: 0;
}

.mainmenu-nav {
  display: flex;
  justify-content: space-between;
}

ul li {
  display: inline-block;
  font-weight: 700;
  padding: 0;
  font-size: 1rem;
}

.navigation ul li a {
  font-weight: 600;
  text-decoration: none;
  color: black;
  padding-left: 0.5rem;
}
.navigation .logo ul {
  padding-inline-start: 0;
}
.navigation ul li {
  font-weight: bold;
}
.navigation a.active {
  border-bottom: 2px solid black;
}